<template>
    <div class="complaintReportingDetail" :class="{themeL:bindThemeL}">
        <!-- 头部 -->
        <navigation-bar  @onLeftClick="onLeftClick" :pageName="title" >
              
        </navigation-bar>

        <!-- 内容 -->
        <section class="WorkContent">
            <div class="workContentBox">
                <div class="workContentWrap">
                     <!-- 基础信息 -->
                        <section class="basicInfoBox">
                            <div class="basicInfo">
                                <div class="basicInfoHeader line">
                                    <div class="headerLeft line" :class="{headerLeftThemeL:bindThemeL}">
                                        <div class="headerIcon"></div>
                                        <span>基础信息</span>
                                    </div>
                                </div>
                                <div class="line basicLine lineOne">
                                    <div class="lineLeft" :class="{lineLeftThemeL:bindThemeL}">工单编号</div>
                                    <div class="lineRight" :class="{lineRightThemeL:bindThemeL}">{{complateDetail.complCode}}</div>
                                </div>
                                <div class="line basicLine">
                                    <div class="lineLeft" :class="{lineLeftThemeL:bindThemeL}">房间信息</div>
                                    <div class="lineRight" :class="{lineRightThemeL:bindThemeL}">{{complateDetail.areaName}}{{complateDetail.buildName}}{{complateDetail.roomName}}</div>
                                </div>
                                <div class="line basicLine">
                                    <div class="lineLeft" :class="{lineLeftThemeL:bindThemeL}">反馈时间</div>
                                    <div class="lineRight" :class="{lineRightThemeL:bindThemeL}">{{complateDetail.createdTime}}</div>
                                </div>
                                <div class="basicLabel">
                                    <div class="rightImgBox">
                                        <div :class="{
                                            rightImg:true,
                                            rightImgThemeL:bindThemeL,
                                            bg1:status == 0,
                                            bg2:status == 1 ,
                                            bg3:status == 2,
                                            bg4:status == 3 ,
                                            }">{{labelTitle}}</div>
                                    </div>
                                    </div>
                            </div>
                        </section>

                        <!-- 问题描述 -->
                        <section class="problemDspBox">
                            <div class="problemDsp">
                                <div class="basicInfoHeader line">
                                    <div class="headerLeft line" :class="{headerLeftThemeL:bindThemeL}">
                                        <div class="headerIcon"></div>
                                        <span>问题描述</span>
                                    </div>
                                    
                                </div>

                                <div class="line problemLabelList">
                                    <div class="labelItem" :class="{labelItemThemeL:bindThemeL}">#{{complateDetail.labelName}}#</div>
                                </div>

                                <div class="problemCon" :class="{problemConThemeL:bindThemeL}">{{complateDetail.content}}</div>

                                <div class="spaceLine"></div>

                                <div class="problemImgList" v-if="proImgList.length > 0">
                                    <div class="proImgItem" v-for="(item,index) in complateDetail.picUrlList" :key="index">
                                        <img :src="item" @click="preview(complateDetail.picUrlList,index)">
                                    </div>
                                </div>
                            </div>
                        </section>
                        
                        <!-- 评价 -->
                        <section class="evaluteBox" v-if="evalFlag">
                            <div class="evalute">
                                <div class="basicInfoHeader line">
                                    <div class="headerLeft line" :class="{headerLeftThemeL:bindThemeL}">
                                        <div class="headerIcon"></div>
                                        <span>工单评价</span>
                                    </div>
                                </div>
                                <div class="line evaluteLine">
                                    <div class="evaluteLeft">
                                        <div class="evaluterImg" v-for="(item,index) in evaluteImgList" :key="index">
                                            <img :src="item" @click="preview(evaluteImgList,index)">
                                        </div>
                                    </div>
                                    <div class="evaluterRgt">{{evalInfo.createdTime}}</div>
                                </div>
                                <div class="evaluteText">{{evalInfo.evalContg}}</div>

                            </div>
                        </section>

                        <!-- 受理过程 -->
                        <section class="acceptanceProcessBox" v-if="complateDetail.detailList != undefined &&complateDetail.detailList.length != 0">
                            <div class="acceptanceProcess">
                                <div class="basicInfoHeader line">
                                    <div class="headerLeft line" :class="{headerLeftThemeL:bindThemeL}">
                                        <div class="headerIcon"></div>
                                        <span>受理过程</span>
                                    </div>    
                                </div>

                                <div class="acceptanceList">
                                    <div class="acceptanceItem" v-for="(detailItem,index) in complateDetail.detailList" :key="index" v-if="detailItem.isShow != 0">
                                        <div class="acceptanceIconBox">
                                            <div :class="{acceptanceIcon:true,redIcon:index == 0}">
                                                <div :class="{acceptanceCircle:true,redCircle:index == 0}"></div>
                                            </div>
                                            <div class="acceptanceLine"></div>
                                        </div>
                                        <div :class="{accaptanceCon:true,accaptanceCon1:index == 0}">
                                            <div class="accaptanceConTop">
                                                <div class="accaptanceSatus" :class="{accaptanceSatusThemeL:bindThemeL}" v-if="detailItem.operaTypeFlag !=9 &&detailItem.operaTypeFlag !=4 &&detailItem.operaTypeFlag !=3 &&detailItem.operaTypeFlag !=5&&detailItem.operaTypeFlag !=6">受理中</div>
                                                <div class="accaptanceSatus" :class="{accaptanceSatusThemeL:bindThemeL}" v-if="detailItem.operaTypeFlag == 3">完成已评价</div>
                                                <div class="accaptanceSatus" :class="{accaptanceSatusThemeL:bindThemeL}" v-if="detailItem.operaTypeFlag == 5">完成未评价</div>
                                                <div class="accaptanceSatus" :class="{accaptanceSatusThemeL:bindThemeL}" v-if="detailItem.operaTypeFlag == 6">受理通知</div>
                                                <div class="accaptanceSatus" :class="{accaptanceSatusThemeL:bindThemeL}" v-if="detailItem.operaTypeFlag == 4">问卷</div>
                                                <div class="accaptanceSatus" :class="{accaptanceSatusThemeL:bindThemeL}" v-if="detailItem.operaTypeFlag == 9">录入情况说明</div>
                                                <div class="accaptanceTime" :class="{accaptanceTimeThemeL:bindThemeL}" >{{detailItem.createdTime}}</div>
                                            </div>
                                            <div class="accaptanceConBottom" :class="{accaptanceConBottomThemeL:bindThemeL}">
                                                <div v-if="detailItem.operaTypeFlag != 6&&detailItem.operaTypeFlag != 3">
                                                    <span v-if="detailItem.postName">{{detailItem.postName}}_</span><span>{{detailItem.userName}}_</span>
                                                    <span class="phoneNum">{{detailItem.phone}}</span></div>
                                                    <div class="handleTxt">{{detailItem.content}}</div>
                                            </div>
                                            <div class="accaptanceImgList" v-if="detailItem.picUrlList != undefined && detailItem.picUrlList.length != 0">
                                                <div class="accaptanceImgItem" v-for="(picUrlItem,index1) in detailItem.picUrlList" :key="index1">
                                                    <img :src="picUrlItem" @click="preview(detailItem.picUrlList,index1)">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>

                        <section class="bottomSpace">

                        </section>
                </div>
               
            </div>
            

        </section>
        
     
        <!-- 底部按钮 -->
        <section class="btnList" v-if="complateDetail.status == 0" :style="{paddingBottom:this.$footBarHeight+'px'}">
           <div :class="{btnItem:true,redBtn:true,btnItemThemeL:bindThemeL}" @click="updateWorkOrder">修改</div>
           <div :class="{btnItem:true,redBtn:false,btnItemThemeL:bindThemeL}" @click="cancel">撤回</div>
           <!-- <div :class="{btnItem:true,redBtn:true}" @click="evaluate">评价</div> -->
        </section>

        <section class="btnList" v-if="complateDetail.status == 1" :style="{paddingBottom:this.$footBarHeight+'px'}">
           <div :class="{btnItem:true,redBtn:true,btnItemThemeL:bindThemeL}" @click="remindWorkOrder">催单</div>
        </section>

        <section class="btnList" v-if="complateDetail.status == 2" :style="{paddingBottom:this.$footBarHeight+'px'}">
           <div :class="{btnItem:true,redBtn:true,btnItemThemeL:bindThemeL}" @click="evaluate">评价</div>
        </section>

     
        <Preview :images="images" ref="myPreview"></Preview>
    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import { ImagePreview, Tab, Dialog, Toast } from 'vant';
import {getWorkOrderDetail,workOrderCancel,remindWorkOrder} from '@/services/servers/compaintReporting'
import RightImg from '@/common/servers/complaintReport/RightImg.vue';
import eventBus from '@/page/Services/ComplaintReporting/eventBus'
import Preview from "@/components/preview/Preview.vue";

export default {
    name:'complaintReportingDetail',
    components:{
        NavigationBar,
        RightImg,
        "Preview": Preview
    },
    mounted(){
        this.init(this.$route.query.complateCode);
        this.complateCode = this.$route.query.complateCode;
        this.typeFlag = this.$route.query.typeFlag;
         if(this.typeFlag == 0){
            this.title = '报事报修'
        }else{
            this.title = "投诉建议"
        }
        
    },
    watch:{
        status:{
            handler(newName, oldName) {
                 switch (newName) {
                    case '0':
                        this.labelTitle = '处理中'
                        break;
                    case '1':
                        this.labelTitle = '处理中'
                        break;
                    case '2':
                        this.labelTitle = '待评价'
                        break;
                    case '3':
                        
                        this.labelTitle = '已完成'

                        break;
                    case '4':
                        this.labelTitle = '已撤销'
                        break;
                };
               
    　　    },
    　　    immediate: true
        },
        images:{
            handler(newName, oldName) {
                
    　　    },
    　　    immediate: true
        },
    },
    data(){
        return{
            title:"报修详情",
            complateCode:'',
            labelTitle:'',
            isClose:8,
            typeFlag:0,
            evalInfo:{},
            evalFlag:false,
            status:0,
            statusFlag:false,
            proImgList:[],
            xingImg:require('@/assets/images/common/icon_evaluate_red.png'),
            xingNoImg: require('@/assets/images/common/icon_evaluate_gray.png'),
            complateDetail:{},
            evaluteImgList:[],
            images:[],
            headerStyle:{
                'position': 'fixed',
                'top': '0',
                'left': '0',
                'right': '0',
                'z-index': '200'
            },
            /*大字模式*/
            bindThemeL: this.$curTheme==0 ? false : true
        }
    },
   
    methods:{
        // 返回
        onLeftClick(){
            this.$router.go(-1);
        },
        
        // 获取详情数据
        init(complateCode){
            let params = {
                'complCode':complateCode
            }
            let that = this
            getWorkOrderDetail(params).then(
                res => {
                   let con = res.data;
                   this.complateDetail = {...con};
                   if(this.complateDetail.evalInfo){
                        this.evalInfo = this.complateDetail.evalInfo;
                        this.evalFlag = true;
                       
                   }
                   that.proImgList = con.picUrlList;
                   that.status = that.complateDetail.status;
                   that.isClose = con.closeType
                   for(let i=0;i<5;i++){
                       if(i < this.evalInfo.evalLevel){
                           this.evaluteImgList.push(this.xingImg)
                       }else{
                           this.evaluteImgList.push(this.xingNoImg)
                       }
                   }
                },
                err =>{

                }
            )
        },

        // 撤销工单
        cancel(){

            let con = {
                'complCode':this.complateDetail.complCode,
                "updatedBy":this.$userInfo.OWNER_CODE,
            };

            Dialog.confirm({
                message: '确定要撤回报修订单？'
            }).then(() => {
                // on confirm
                workOrderCancel(con).then(
                    res =>{
                        if(res.resultCode == '0'){
                            Toast('撤销成功');
                            this.$router.go(-1);
                        }else{
                            Toast(res.msg)
                        }
                    },
                    req => {
                        
                    }
                )
            }).catch(() => {
                // on cancel
            });

           
            
        },
        preview(src, index) {
            this.images = src;
           
            this.$refs.myPreview.setIndex(index)
            this.$refs.myPreview.setIsShowPreview(true);
            this.$store.dispatch("setPreviewId", this.$refs.myPreview);
        },

        // 修改工单
        updateWorkOrder(){
            this.$router.push({
                name: 'complaintReportingAdd',
                query: {
                  complateCode:this.complateDetail.complCode,
                  typeFlag:this.typeFlag
                }
            })
        },

        // 催单
        remindWorkOrder(){
            let data = {
                "complCode": this.complateDetail.complCode,
                "remindOwnerCode": this.$userInfo.OWNER_CODE,
                "remindOwnerName": this.$userInfo.OWNER_NAME,
                "remindOwnerPhone": this.$userInfo.PHONE,
            };

            remindWorkOrder(data).then(
                res => {
                    if (res.resultCode == 0) {
                        Toast('催单成功');
                    } else {
                       Toast(res.msg)
                    }
                },
                err => {
                   
                }
            )
        },

        // 评价
        evaluate(){
            this.$router.push({
                name: 'complaintReportingEvaluate',
                query: {
                  complateCode:this.complateDetail.complCode,
                }
            })
        }
        
        
    }
}
</script>

