<template>
    <div class="rightImgBox">
        <div :class="{
            rightImg:true,
            rightImgThemeL:bindThemeFont,
            bg1:labelType == 0,
            bg2:labelType == 1 ,
            bg3:labelType == 2,
            bg4:labelType == 3,
            }">{{labelTitle}}</div>
    </div>
      <!-- <div class="workOrderType workOrderSubmit" ng-if="workOrderInfo.status==0">已提交</div>
                    <div class="workOrderType workOrderProcessing" ng-if="workOrderInfo.status==1||workOrderInfo.closeType==2">受理中</div>
                    <div class="workOrderType workOrderComment" ng-if="workOrderInfo.status==2">待评价</div>
                    <div class="workOrderType workOrderCompleted" ng-if="workOrderInfo.status==3&&workOrderInfo.closeType!=2">已完成</div>
                    <div class="workOrderType workOrderCompleted" ng-if="workOrderInfo.status==4">已撤销</div> -->
</template>

<script>
export default {
    name:'RightImg',
    props:{
        // 类型
        labelType:{
            type:String,
            default:'8'
        },
        isClose:{
            default:'8'
        }
    },
    data(){
        return{
            labelTitle:'处理中',
            /*大字模式*/
            bindThemeFont: this.$curTheme==0 ? false : true
        }
    },
    mounted(){
        this.init();
    },
    methods:{
        init(){
            switch (this.labelType) {
                case '0':
                    this.labelTitle = '处理中'
                    break;
                case '1':
                    this.labelTitle = '处理中'
                    break;
                case '2':
                    this.labelTitle = '待评价'
                    break;
                case '3':
                   this.labelTitle = '已完成'
                    break;
                case '4':
                    this.labelTitle = '已撤销'
                    break;
            };
            // if(this.isClose == 2){
            //     this.labelTitle = '受理中'
            // }
        }
    }
}
</script>

<style scoped>
.rightImgBox{
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.rightImgBox .rightImg{
     width: 100%;
    height: 100%;
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: -0.04px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.rightImgBox .rightImgThemeL{
    font-size: 15px !important;
}
.rightImgBox .bg1{
   background-image: url('../../../assets/images/home/property_order/tag_processing.png')
}
.rightImgBox .bg2{
    background-image: url('../../../assets/images/home/property_order/tag_processing.png')
}
.rightImgBox .bg3{
    background-image: url('../../../assets/images/home/property_order/tag_comment.png')
}
.rightImgBox .bg4{
    background-image: url('../../../assets/images/home/property_order/tag_completed.png')
}
</style>